import { useCallback } from 'react';
import { apiService } from '../../../services/api';

export const useWarehouses = (organizationId, setWarehouses, setSnackbar) => {
  /**   */
  const showSnackbar = useCallback((message, severity = 'success') => {
    setSnackbar({ open: true, message, severity });
    setTimeout(() => {
      setSnackbar({ open: false, message: '', severity: 'success' });
    }, 3000);
  }, [setSnackbar]);

  /**   */
  const handleCreateWarehouse = async (warehouseData) => {
    try {
      const newWarehouse = await apiService.createWarehouse(warehouseData);
      setWarehouses(prev => {
        if (!Array.isArray(prev)) {
          console.warn(' Warehouses state is not array, initializing:', prev);
          return [newWarehouse];
        }
        return [...prev, newWarehouse];
      });
      
      showSnackbar('  ');
      return newWarehouse;
    } catch (error) {
      console.error('   :', error);
      showSnackbar('   ', 'error');
      throw error;
    }
  };

  /**   */
  const handleUpdateWarehouse = async (warehouseId, data) => {
    try {
      const updatedWarehouse = await apiService.updateWarehouse(warehouseId, data);
      
      setWarehouses(prev => {
        if (!Array.isArray(prev)) {
          console.warn(' Warehouses state is not array during update:', prev);
          return [updatedWarehouse];
        }
        return prev.map(warehouse => 
          warehouse.id === warehouseId ? updatedWarehouse : warehouse
        );
      });
      
      showSnackbar('  ');
      return updatedWarehouse;
    } catch (error) {
      console.error('   :', error);
      showSnackbar('   ', 'error');
      throw error;
    }
  };

  /**   */
  const handleDeleteWarehouse = async (warehouseId) => {
    if (!window.confirm(' ,     ?')) {
      return;
    }

    try {
      await apiService.deleteWarehouse(warehouseId);
      
      setWarehouses(prev => {
        if (!Array.isArray(prev)) {
          console.warn(' Warehouses state is not array during delete:', prev);
          return [];
        }
        return prev.filter(warehouse => warehouse.id !== warehouseId);
      });
      
      showSnackbar('  ');
    } catch (error) {
      console.error('   :', error);
      showSnackbar('   ', 'error');
      throw error;
    }
  };

  return {
    handleCreateWarehouse,
    handleUpdateWarehouse,
    handleDeleteWarehouse
  };
};