// src/components/LoginPage/LoginPage.jsx
import React, { useState, useEffect } from 'react';
import { useTheme } from '../../../theme/ThemeContext';
import { useAuth } from '../../../context/AuthContext';
import { useNavigate, useSearchParams } from 'react-router-dom';
import './LoginPage.scss';

export default function LoginPage() {
  const { theme, isDark, toggleTheme } = useTheme();
  const { login, loading } = useAuth();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const [formData, setFormData] = useState({
    email: '',
    password: '',
  });

  const [showPassword, setShowPassword] = useState(false);
  const [error, setError] = useState('');
  const [hasInviteToken, setHasInviteToken] = useState(false);

  /**       URL   */
  useEffect(() => {
    const inviteToken = searchParams.get('invite');
    if (inviteToken) {
      localStorage.setItem('invite_token', inviteToken);
      setHasInviteToken(true);
    } else {
      // ,     
      const savedToken = localStorage.getItem('invite_token');
      setHasInviteToken(!!savedToken);
    }
  }, [searchParams]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    setError('');
  };

  const validateForm = () => {
    if (!formData.email || !formData.password) {
      setError('Please fill in all fields');
      return false;
    }
    
    if (!formData.email.includes('@')) {
      setError('Please enter a valid email address');
      return false;
    }
    
    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setError('');

    const result = await login(formData);

    if (result.success) {
      //   -   
      navigate('/dashboard');
    } else {
      setError(result.error || 'Login failed. Please try again.');
    }
  };
  
  const isFormFilled = formData.email && formData.password;

  return (
    <div className="login-page" style={{ backgroundColor: theme.PAGE_BG }}>
      <div className="login-card" style={{ backgroundColor: theme.CARD }}>
        <div className="login-form">
          <div 
            className="login-header" 
            style={{ borderBottomColor: isDark ? '#ffffff' : theme.TITLE_TEXT }}
          >
            <h1 className="login-title" style={{ color: theme.TITLE_TEXT }}>Login</h1>
            <span className="ogate-label" style={{ color: theme.TITLE_TEXT }}>O-Gate</span>
          </div>

          {hasInviteToken && (
            <div style={{ 
              color: theme.ACCENT, 
              marginBottom: '15px', 
              textAlign: 'center',
              padding: '10px',
              backgroundColor: 'rgba(0, 123, 255, 0.1)',
              borderRadius: '5px',
              border: `1px solid ${theme.ACCENT}`
            }}>
                 
            </div>
          )}

          {error && (
            <div style={{ 
              color: '#ff6b6b', 
              marginBottom: '15px', 
              textAlign: 'center',
              padding: '10px',
              backgroundColor: 'rgba(255,107,107,0.1)',
              borderRadius: '5px',
              border: '1px solid #ff6b6b'
            }}>
              {error}
            </div>
          )}

          <form className="login-fields" onSubmit={handleSubmit}>
            <input
              type="email"
              name="email"
              placeholder="Email"
              className={`login-input ${formData.email ? 'filled' : ''}`}
              value={formData.email}
              onChange={handleChange}
               style={{
                borderColor: formData.email ? theme.ACCENT : theme.INPUT_BORDER,
                backgroundColor: theme.INPUT_BG,
                color: theme.INPUT_TEXT,
              }}
            />

            <div className={`password-field ${formData.password ? 'filled' : ''}`}>
              <input
                type={showPassword ? 'text' : 'password'}
                name="password"
                placeholder="Password"
                className="login-input"
                value={formData.password}
                onChange={handleChange}
               style={{
                borderColor: formData.email ? theme.ACCENT : theme.INPUT_BORDER,
                backgroundColor: theme.INPUT_BG,
                color: theme.INPUT_TEXT,
              }}
              />
              <img
                src={showPassword ? "/assets/LoginPage/eye-on.png" : "/assets/LoginPage/eye-off.png"}
                alt="toggle password"
                className="eye-icon"
                onClick={() => setShowPassword(prev => !prev)}
                style={{ filter: theme.EYE_ICON_FILTER }}
              />
            </div>

            {/*        Enter */}
            <button
              type="submit"
              className={`login-btn ${isFormFilled ? 'active' : ''}`}
              disabled={!isFormFilled || loading}
              style={{
                backgroundColor: isFormFilled ? theme.ACCENT : theme.BTN,
                color: isFormFilled ? '#000' : theme.LABEL_TEXT,
                marginTop: '20px'
              }}
            >
              {loading ? 'Logging in...' : 'Login'}
            </button>
          </form>

          <div 
            className="login-footer" 
            style={{ borderTopColor: isDark ? '#ffffff' : theme.TITLE_TEXT }}
          >
            <p className="login-link" style={{ color: theme.LABEL_TEXT }}>
              Don't have an account?{' '}
              <span 
                onClick={() => navigate('/register')} 
                style={{ 
                  color: theme.ACCENT, 
                  cursor: 'pointer', 
                  fontWeight: 'bold',
                  textDecoration: 'underline'
                }}
              >
                Register
              </span>
            </p>
          </div>
        </div>
      </div>

      <div className="right-art">
        <img 
          src={`/assets/LoginPage/oriole${isDark ? '_b' : '_l'}.svg`} 
          alt="O-Gate" 
        />
      </div>

      <button className="theme-toggle-btn" onClick={toggleTheme}>
        <img
          src={`/assets/LoginPage/${isDark ? 'sun' : 'moon'}.svg`}
          alt="Toggle theme"
        />
      </button>
    </div>
  );
}