// src/components/RegisterPage/RegisterPage.jsx
import React, { useState, useEffect } from 'react';
import { useTheme } from '../../../theme/ThemeContext';
import { useAuth } from '../../../context/AuthContext';
import { useNavigate, useSearchParams } from 'react-router-dom';
import './RegisterPage.scss';

export default function RegisterPage() {
  const { theme, isDark, toggleTheme } = useTheme();
  const { register, loading } = useAuth();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    phone: '',
    password: '',
    companyName: '',
    role: '',
  });

  const [showPassword, setShowPassword] = useState(false);
  const [errors, setErrors] = useState({});
  const [hasInviteToken, setHasInviteToken] = useState(false);

  /**       URL   */
  useEffect(() => {
    const inviteToken = searchParams.get('invite');
    if (inviteToken) {
      localStorage.setItem('invite_token', inviteToken);
      setHasInviteToken(true);
    } else {
      // ,     
      const savedToken = localStorage.getItem('invite_token');
      setHasInviteToken(!!savedToken);
    }
  }, [searchParams]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    
    setFormData(prev => ({ ...prev, [name]: value }));
    
    if (errors[name]) {
      setErrors(prev => ({ ...prev, [name]: '' }));
    }
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.fullName?.trim()) newErrors.fullName = 'Full name is required';
    if (!formData.email?.trim()) newErrors.email = 'Email is required';
    if (!formData.phone?.trim()) newErrors.phone = 'Phone is required';
    if (!formData.password?.trim()) newErrors.password = 'Password is required';
    if (!formData.companyName?.trim()) newErrors.companyName = 'Company name is required';
    if (!formData.role?.trim()) newErrors.role = 'Role is required';
    
    if (formData.email && !formData.email.includes('@')) {
      newErrors.email = 'Please enter a valid email address';
    }
    
    if (formData.phone && !/^\+?[\d\s\-\(\)]+$/.test(formData.phone)) {
      newErrors.phone = 'Please enter a valid phone number';
    }
    
    if (formData.password && formData.password.length < 6) {
      newErrors.password = 'Password must be at least 6 characters';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  //  RegisterPage.jsx  handleSubmit
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setErrors({});

    const result = await register(formData);

    if (result.success) {
      //  email  
      localStorage.setItem('pendingVerificationEmail', formData.email);
      //    
      navigate('/verify', { state: { email: formData.email } });
    } else {
      setErrors({ general: result.error || 'Registration failed. Please try again.' });
    }
  };

  const isFormFilled = Object.values(formData).every(v => v && v.trim() !== '');

  return (
    <div className="register-page" style={{ backgroundColor: theme.PAGE_BG }}>
      <div className="register-card" style={{ backgroundColor: theme.CARD }}>
        <div className="register-form">
          <div 
            className="register-header" 
            style={{ borderBottomColor: isDark ? '#ffffff' : theme.TITLE_TEXT }}
          >
            <h1 className="register-title" style={{ color: theme.TITLE_TEXT }}>Register</h1>
            <span className="ogate-label" style={{ color: theme.TITLE_TEXT }}>O-Gate</span>
          </div>

          {hasInviteToken && (
            <div style={{ 
              color: theme.ACCENT, 
              marginBottom: '15px', 
              textAlign: 'center',
              padding: '10px',
              backgroundColor: 'rgba(0, 123, 255, 0.1)',
              borderRadius: '5px',
              border: `1px solid ${theme.ACCENT}`
            }}>
                 
            </div>
          )}

          {errors.general && (
            <div className="error-message" style={{
              padding: '10px',
              backgroundColor: 'rgba(255, 107, 107, 0.1)',
              borderRadius: '6px',
              fontSize: '13px',
              textAlign: 'center',
              color: '#ff6b6b',
              marginBottom: '10px',
              border: '1px solid #ff6b6b'
            }}>
              {errors.general}
            </div>
          )}

          <form className="register-fields" onSubmit={handleSubmit}>
            <input
              type="text"
              name="fullName"
              placeholder="Full Name"
              className={`register-input ${errors.fullName ? 'input-error' : ''} ${formData.fullName ? 'filled' : ''}`}
              value={formData.fullName}
              onChange={handleChange}
               style={{
                borderColor: formData.email ? theme.ACCENT : theme.INPUT_BORDER,
                backgroundColor: theme.INPUT_BG,
                color: theme.INPUT_TEXT,
              }}
            />
            {errors.fullName && <div className="error-message">{errors.fullName}</div>}

            <input
              type="email"
              name="email"
              placeholder="Email"
              className={`register-input ${errors.email ? 'input-error' : ''} ${formData.email ? 'filled' : ''}`}
              value={formData.email}
              onChange={handleChange}
               style={{
                borderColor: formData.email ? theme.ACCENT : theme.INPUT_BORDER,
                backgroundColor: theme.INPUT_BG,
                color: theme.INPUT_TEXT,
              }}
            />
            {errors.email && <div className="error-message">{errors.email}</div>}

            <input
              type="tel"
              name="phone"
              placeholder="Phone"
              className={`register-input ${errors.phone ? 'input-error' : ''} ${formData.phone ? 'filled' : ''}`}
              value={formData.phone}
              onChange={handleChange}
               style={{
                borderColor: formData.email ? theme.ACCENT : theme.INPUT_BORDER,
                backgroundColor: theme.INPUT_BG,
                color: theme.INPUT_TEXT,
              }}
            />
            {errors.phone && <div className="error-message">{errors.phone}</div>}

            <input
              type="text"
              name="companyName"
              placeholder="Company Name"
              className={`register-input ${errors.companyName ? 'input-error' : ''} ${formData.companyName ? 'filled' : ''}`}
              value={formData.companyName}
              onChange={handleChange}
               style={{
                borderColor: formData.email ? theme.ACCENT : theme.INPUT_BORDER,
                backgroundColor: theme.INPUT_BG,
                color: theme.INPUT_TEXT,
              }}
            />
            {errors.companyName && <div className="error-message">{errors.companyName}</div>}

            <select
              name="role"
              className={`role-select ${errors.role ? 'input-error' : ''} ${formData.role ? 'filled' : ''}`}
              value={formData.role}
              onChange={handleChange}
              style={{
                borderColor: errors.role ? '#ff6b6b' : (formData.role ? theme.ACCENT : theme.INPUT_BORDER),
                color: formData.role ? theme.LABEL_TEXT : '#ccc',
                backgroundColor: theme.CARD,
              }}
            >
              <option value="" disabled>Select Role</option>
              <option value="Founder">Founder</option>
              <option value="Checker">Checker</option>
            </select>
            {errors.role && <div className="error-message">{errors.role}</div>}

            <div className={`password-field ${formData.password ? 'filled' : ''}`}>
              <input
                type={showPassword ? 'text' : 'password'}
                name="password"
                placeholder="Password"
                className={`register-input ${errors.password ? 'input-error' : ''}`}
                value={formData.password}
                onChange={handleChange}
               style={{
                borderColor: formData.email ? theme.ACCENT : theme.INPUT_BORDER,
                backgroundColor: theme.INPUT_BG,
                color: theme.INPUT_TEXT,
              }}
              />
              <img
                src={showPassword ? "/assets/LoginPage/eye-on.png" : "/assets/LoginPage/eye-off.png"}
                alt="toggle password"
                className="eye-icon"
                onClick={() => setShowPassword(prev => !prev)}
                style={{ filter: theme.EYE_ICON_FILTER }}
              />
            </div>
            {errors.password && <div className="error-message">{errors.password}</div>}
          </form>

          <div 
            className="register-footer" 
            style={{ borderTopColor: isDark ? '#ffffff' : theme.TITLE_TEXT }}
          >
            <p className="register-link" style={{ color: theme.LABEL_TEXT }}>
              Already have an account?{' '}
              <span 
                onClick={() => navigate('/login')} 
                style={{ 
                  color: theme.ACCENT, 
                  cursor: 'pointer', 
                  fontWeight: 'bold',
                  textDecoration: 'underline'
                }}
              >
                Login
              </span>
            </p>
            
            <button
              type="submit"
              className={`register-btn ${isFormFilled ? 'active' : ''}`}
              disabled={!isFormFilled || loading}
              style={{
                backgroundColor: isFormFilled ? theme.ACCENT : theme.BTN,
                color: isFormFilled ? '#000' : theme.LABEL_TEXT,
              }}
              onClick={handleSubmit}
            >
              {loading ? 'Creating...' : 'Register'}
            </button>
          </div>
        </div>
      </div>

      <div className="right-art">
        <img 
          src={`/assets/LoginPage/oriole${isDark ? '_b' : '_l'}.svg`} 
          alt="O-Gate" 
        />
      </div>

      <button className="theme-toggle-btn" onClick={toggleTheme}>
        <img
          src={`/assets/LoginPage/${isDark ? 'sun' : 'moon'}.svg`}
          alt="Toggle theme"
        />
      </button>
    </div>
  );
}