// components/dialogs/InventoryReportDialog.jsx
import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Box,
  Typography,
  Alert,
  CircularProgress,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
  Tabs,
  Tab
} from '@mui/material';
import { Download as DownloadIcon, Visibility as VisibilityIcon } from '@mui/icons-material';
import { apiService } from '../../services/api';

const InventoryReportDialog = ({ open, onClose, sklad, sklad_id, warehouseName }) => {
  const [reportData, setReportData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [isEmpty, setIsEmpty] = useState(false);
  const [downloading, setDownloading] = useState(false);
  const [viewMode, setViewMode] = useState('standard'); // 'standard'  'token'
  const [token, setToken] = useState('');
  const [reportGeneration, setReportGeneration] = useState(null); // { message, url, qr, path, filename }

  //      (  useEffect)
  const getTokenFromGeneration = (generationData) => {
    //      
    if (generationData.token) {
      return generationData.token;
    }
    
    //     URL ( URL  /view/{token})
    const url = generationData.url || generationData.path;
    if (url) {
      console.log('   URL:', url);
      
      //   /view/{token}  /inventory/view/{token}  URL
      const viewMatch = url.match(/\/view\/([0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12})/i);
      if (viewMatch && viewMatch[1]) {
        console.log('   URL (view):', viewMatch[1]);
        return viewMatch[1];
      }
      
      //   /report/inventory/view/{token}  URL
      const reportViewMatch = url.match(/\/report\/inventory\/view\/([0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12})/i);
      if (reportViewMatch && reportViewMatch[1]) {
        console.log('   URL (report/inventory/view):', reportViewMatch[1]);
        return reportViewMatch[1];
      }
    }
    
    //      
    // : inventory_org_{org_id}_{date}.pdf  inventory_sklad_{sklad_id}_{date}.pdf
    const filename = generationData.filename || generationData.path || generationData.url;
    if (filename) {
      console.log('  :', filename);
      
      //   inventory_sklad_{token}_  inventory_org_{token}_
      const skladMatch = filename.match(/inventory_sklad_([0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12})_/i);
      if (skladMatch && skladMatch[1]) {
        console.log('  :', skladMatch[1]);
        return skladMatch[1];
      }
      
      const orgMatch = filename.match(/inventory_org_([0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12})_/i);
      if (orgMatch && orgMatch[1]) {
        console.log('  :', orgMatch[1]);
        return orgMatch[1];
      }
      
      //    ,   UUID  inventory_org_  inventory_sklad_
      const generalMatch = filename.match(/inventory_(?:org|sklad)_([0-9a-f-]{36})/i);
      if (generalMatch && generalMatch[1]) {
        console.log('   :', generalMatch[1]);
        return generalMatch[1];
      }
    }
    
    //      ,   UUID   (   ,  ID /)
    //     ,     
    console.warn('     . URL:', url, 'Filename:', filename);
    return null;
  };

  useEffect(() => {
    const loadReport = async () => {
      if (!open) {
        setReportData(null);
        setError(null);
        setIsEmpty(false);
        setToken('');
        setViewMode('standard');
        setReportGeneration(null);
        return;
      }

      //      
      if (viewMode === 'standard' && !reportGeneration) {
        setLoading(true);
        setError(null);
        setIsEmpty(false);
        try {
          const data = await apiService.getInventoryReport(sklad, sklad_id);
          console.log('  getInventoryReport:', data, ':', typeof data);
          
          //   -  (),   
          if (typeof data === 'string' && data.trim()) {
            console.log('   :', data);
            const tokenValue = data.trim();
            
            //     
            try {
              const reportData = await apiService.viewInventoryReportByToken(tokenValue);
              console.log('    viewInventoryReportByToken:', reportData);
              setReportData(reportData);
              setIsEmpty(false);
              setError(null);
            } catch (reportError) {
              console.error('    :', reportError);
              setError('     ');
              setReportData(null);
            }
          }
          //   - 
          else if (data && typeof data === 'object') {
            //       ,     
            if (data.token && typeof data.token === 'string') {
              console.log('   :', data.token);
              //    (  qr, url  ..)
              if (data.qr || data.url || data.message) {
                setReportGeneration(data);
              }
              setReportData(null);
              
              //     
              try {
                const reportData = await apiService.viewInventoryReportByToken(data.token);
                console.log('    viewInventoryReportByToken:', reportData);
                setReportData(reportData);
                setIsEmpty(false);
                setError(null);
              } catch (reportError) {
                console.error('    :', reportError);
                setError('     ');
                setReportData(null);
              }
            }
            // ,       ( )
            else if (data.qr || data.url || data.message) {
              //   ,   
              setReportGeneration(data);
              setReportData(null);
              
              //      
              const tokenValue = getTokenFromGeneration(data);
              console.log('    :', tokenValue);
              if (tokenValue) {
                try {
                  const reportData = await apiService.viewInventoryReportByToken(tokenValue);
                  console.log('    viewInventoryReportByToken:', reportData);
                  setReportData(reportData);
                  setIsEmpty(false);
                  setError(null);
                } catch (reportError) {
                  console.error('    :', reportError);
                  setError('     ');
                  setReportData(null);
                }
              } else {
                console.warn('      ');
                setError('       ');
              }
            } else {
              //   
              setReportData(data);
              setIsEmpty(false);
            }
          } else {
            //    (    )
            setReportData(data);
            setIsEmpty(false);
          }
        } catch (error) {
          console.error('  :', error);
          // ,   404    ,   
          const errorMessage = error.message || '';
          if (errorMessage.includes('404') && errorMessage.includes('No inventory data found')) {
            setIsEmpty(true);
            setError(null);
            setReportData(null);
          } else {
            setError('     ');
            setIsEmpty(false);
          }
        } finally {
          setLoading(false);
        }
      }
    };

    loadReport();
  }, [open, sklad, sklad_id, viewMode]);

  const handleLoadByTokenWithToken = async (tokenValue) => {
    if (!tokenValue) {
      setError('  ');
      return;
    }

    setLoading(true);
    setError(null);
    setIsEmpty(false);
    try {
      const data = await apiService.viewInventoryReportByToken(tokenValue);
      setReportData(data);
      setIsEmpty(false);
    } catch (error) {
      console.error('    :', error);
      const errorMessage = error.message || '';
      if (errorMessage.includes('404')) {
        setError('     ');
      } else {
        setError('     ');
      }
      setIsEmpty(false);
    } finally {
      setLoading(false);
    }
  };

  const handleLoadByToken = async () => {
    if (!token.trim()) {
      setError('    ');
      return;
    }

    await handleLoadByTokenWithToken(token.trim());
  };

  const renderReportContent = () => {
    if (loading) {
      return (
        <Box display="flex" flexDirection="column" alignItems="center" gap={2} py={4}>
          <CircularProgress />
          <Typography variant="body2" color="textSecondary">
             ...
          </Typography>
        </Box>
      );
    }

    if (isEmpty) {
      return (
        <Alert severity="info" sx={{ width: '100%' }}>
                
        </Alert>
      );
    }

    if (error) {
      return (
        <Alert severity="error" sx={{ width: '100%' }}>
          {error}
        </Alert>
      );
    }

    if (!reportData) {
      return (
        <Alert severity="info" sx={{ width: '100%' }}>
             
        </Alert>
      );
    }

    //   - ,  
    if (Array.isArray(reportData)) {
      if (reportData.length === 0) {
        return (
          <Alert severity="info" sx={{ width: '100%' }}>
               
          </Alert>
        );
      }

      //    
      const keys = Object.keys(reportData[0] || {});
      
      return (
        <TableContainer component={Paper} sx={{ maxHeight: 600 }}>
          <Table stickyHeader>
            <TableHead>
              <TableRow>
                {keys.map((key) => (
                  <TableCell key={key} sx={{ fontWeight: 'bold' }}>
                    {key}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {reportData.map((row, index) => (
                <TableRow key={index}>
                  {keys.map((key) => (
                    <TableCell key={key}>
                      {typeof row[key] === 'object' ? JSON.stringify(row[key]) : String(row[key] || '')}
                    </TableCell>
                  ))}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      );
    }

    // ,       (    )
    if (reportData && typeof reportData === 'object' && (reportData.qr || reportData.url || reportData.message)) {
      return (
        <Alert severity="warning" sx={{ width: '100%' }}>
                . ,     .
        </Alert>
      );
    }

    //   - ,   JSON
    return (
      <Box>
        <Typography variant="body2" component="pre" sx={{ 
          whiteSpace: 'pre-wrap', 
          wordBreak: 'break-word',
          backgroundColor: '#f5f5f5',
          padding: 2,
          borderRadius: 1,
          maxHeight: 400,
          overflow: 'auto'
        }}>
          {JSON.stringify(reportData, null, 2)}
        </Typography>
      </Box>
    );
  };

  return (
    <Dialog
      open={open}
      onClose={onClose}
      maxWidth="lg"
      fullWidth
    >
      <DialogTitle>
        <Box>
          <Typography variant="h6" component="div">
              
          </Typography>
          {warehouseName && (
            <Typography variant="body2" color="textSecondary" component="div">
              {warehouseName}
            </Typography>
          )}
        </Box>
      </DialogTitle>

      <DialogContent>
        <Box sx={{ mt: 2 }}>
          {/*   */}
          <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 2 }}>
            <Tabs value={viewMode} onChange={(e, newValue) => setViewMode(newValue)}>
              <Tab label=" " value="standard" />
              <Tab label="  " value="token" />
            </Tabs>
          </Box>

          {/*     (   token) */}
          {viewMode === 'token' && (
            <Box sx={{ mb: 2 }}>
              <TextField
                fullWidth
                label=" "
                value={token}
                onChange={(e) => setToken(e.target.value)}
                placeholder="    "
                variant="outlined"
                sx={{ mb: 2 }}
              />
              <Button
                variant="contained"
                startIcon={<VisibilityIcon />}
                onClick={handleLoadByToken}
                disabled={loading || !token.trim()}
                fullWidth
              >
                   
              </Button>
            </Box>
          )}

          {/*     (QR-    ) */}
          {reportGeneration && (
            <Box sx={{ mb: 3, p: 2, border: '1px solid', borderColor: 'divider', borderRadius: 1, backgroundColor: '#f9f9f9' }}>
              <Alert severity="success" sx={{ mb: 2 }}>
                {reportGeneration.message || '  !'}
              </Alert>
              
              <Typography variant="subtitle2" gutterBottom sx={{ mb: 2 }}>
                 :
              </Typography>
              
              {/* QR- */}
              {reportGeneration.qr && (
                <Box sx={{ mb: 2, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                  <Typography variant="body2" color="textSecondary" gutterBottom>
                    QR-   :
                  </Typography>
                  <Box
                    component="img"
                    src={`data:image/png;base64,${reportGeneration.qr}`}
                    alt="QR Code"
                    sx={{
                      width: 150,
                      height: 150,
                      border: '1px solid',
                      borderColor: 'divider',
                      borderRadius: 1,
                    }}
                  />
                  <Typography variant="caption" color="textSecondary" sx={{ mt: 1 }}>
                     QR-   
                  </Typography>
                </Box>
              )}

              {/*  */}
              {reportGeneration.url && (
                <Box sx={{ mb: 2 }}>
                  <Typography variant="body2" color="textSecondary" gutterBottom>
                       :
                  </Typography>
                  <TextField
                    fullWidth
                    size="small"
                    value={reportGeneration.url}
                    InputProps={{
                      readOnly: true,
                    }}
                    variant="outlined"
                    sx={{
                      backgroundColor: 'white',
                    }}
                  />
                  <Typography variant="caption" color="textSecondary" sx={{ mt: 0.5, display: 'block' }}>
                         
                  </Typography>
                </Box>
              )}

              {/*    */}
              {reportGeneration.filename && (
                <Typography variant="body2" color="textSecondary">
                  : {reportGeneration.filename}
                </Typography>
              )}
            </Box>
          )}

          {/*   */}
          {renderReportContent()}
        </Box>
      </DialogContent>

      <DialogActions>
        <Button onClick={onClose}>
          
        </Button>
        {/*       */}
        {viewMode === 'standard' && (
          <>
            <Button
              startIcon={<DownloadIcon />}
              onClick={async () => {
                setDownloading(true);
                setError(null);
                setReportGeneration(null);
                setReportData(null);
                try {
                  const result = await apiService.downloadInventoryReport(sklad, sklad_id);
                  console.log('  downloadInventoryReport:', result);
                  
                  //   JSON    
                  if (result && typeof result === 'object') {
                    //     ,   
                    if (result.token && typeof result.token === 'string') {
                      console.log('    downloadInventoryReport:', result.token);
                      setReportGeneration(result);
                      
                      //       
                      setLoading(true);
                      setError(null);
                      setIsEmpty(false);
                      try {
                        const reportData = await apiService.viewInventoryReportByToken(result.token);
                        console.log('  :', reportData);
                        setReportData(reportData);
                        setIsEmpty(false);
                        setError(null);
                      } catch (reportError) {
                        console.error('    :', reportError);
                        setError('     .    "  "');
                        setReportData(null);
                      } finally {
                        setLoading(false);
                      }
                    }
                    //   qr  url,   ,  
                    else if (result.qr && result.url) {
                      setReportGeneration(result);
                      
                      //       
                      const tokenValue = getTokenFromGeneration(result);
                      console.log(' :', tokenValue, ' :', result);
                      if (tokenValue) {
                        setLoading(true);
                        setError(null);
                        setIsEmpty(false);
                        try {
                          const reportData = await apiService.viewInventoryReportByToken(tokenValue);
                          console.log('  :', reportData);
                          setReportData(reportData);
                          setIsEmpty(false);
                          setError(null);
                        } catch (reportError) {
                          console.error('    :', reportError);
                          setError('     .    "  "');
                          setReportData(null);
                        } finally {
                          setLoading(false);
                        }
                      } else {
                        console.warn('      :', result);
                        setError('       ');
                      }
                    }
                  }
                  //     , result  { success: true, filename }
                } catch (error) {
                  console.error('  :', error);
                  const errorMessage = error.message || '';
                  if (errorMessage.includes('404') && errorMessage.includes('No inventory data found')) {
                    setError('    ');
                  } else {
                    setError('     ');
                  }
                } finally {
                  setDownloading(false);
                }
              }}
              variant="contained"
              color="primary"
              disabled={downloading || loading}
            >
              {downloading ? '...' : ' '}
            </Button>
            <Button
              onClick={async () => {
                setLoading(true);
                setError(null);
                setIsEmpty(false);
                setReportGeneration(null);
                setReportData(null);
                try {
                  const data = await apiService.getInventoryReport(sklad, sklad_id);
                  console.log('  getInventoryReport ():', data, ':', typeof data);
                  
                  //   -  (),   
                  if (typeof data === 'string' && data.trim()) {
                    console.log('    ():', data);
                    const tokenValue = data.trim();
                    
                    //     
                    try {
                      const reportData = await apiService.viewInventoryReportByToken(tokenValue);
                      console.log('    viewInventoryReportByToken ():', reportData);
                      setReportData(reportData);
                      setIsEmpty(false);
                      setError(null);
                    } catch (reportError) {
                      console.error('     ():', reportError);
                      setError('     ');
                      setReportData(null);
                    }
                  }
                  //   - 
                  else if (data && typeof data === 'object') {
                    //       ,     
                    if (data.token && typeof data.token === 'string') {
                      console.log('    ():', data.token);
                      //    (  qr, url  ..)
                      if (data.qr || data.url || data.message) {
                        setReportGeneration(data);
                      }
                      
                      //     
                      try {
                        const reportData = await apiService.viewInventoryReportByToken(data.token);
                        console.log('    viewInventoryReportByToken ():', reportData);
                        setReportData(reportData);
                        setIsEmpty(false);
                        setError(null);
                      } catch (reportError) {
                        console.error('     ():', reportError);
                        setError('     ');
                        setReportData(null);
                      }
                    }
                    // ,       ( )
                    else if (data.qr || data.url || data.message) {
                      //   ,   
                      setReportGeneration(data);
                      
                      //      
                      const tokenValue = getTokenFromGeneration(data);
                      console.log('     ():', tokenValue);
                      if (tokenValue) {
                        try {
                          const reportData = await apiService.viewInventoryReportByToken(tokenValue);
                          console.log('    viewInventoryReportByToken ():', reportData);
                          setReportData(reportData);
                          setIsEmpty(false);
                          setError(null);
                        } catch (reportError) {
                          console.error('     ():', reportError);
                          setError('     ');
                          setReportData(null);
                        }
                      } else {
                        console.warn('       ()');
                        setError('       ');
                      }
                    } else {
                      //   
                      setReportData(data);
                      setIsEmpty(false);
                    }
                  } else {
                    //    (    )
                    setReportData(data);
                    setIsEmpty(false);
                  }
                } catch (error) {
                  console.error('   ():', error);
                  const errorMessage = error.message || '';
                  if (errorMessage.includes('404') && errorMessage.includes('No inventory data found')) {
                    setIsEmpty(true);
                    setError(null);
                    setReportData(null);
                  } else {
                    setError('     ');
                    setIsEmpty(false);
                  }
                } finally {
                  setLoading(false);
                }
              }}
              variant="outlined"
              disabled={loading || downloading}
            >
              
            </Button>
          </>
        )}
      </DialogActions>
    </Dialog>
  );
};

export default InventoryReportDialog;

