  async getInventoryReport(sklad, sklad_id) {
    const url = `${this.baseURL}/report/inventory`;
    const token = localStorage.getItem('authToken');
    
    const body = { sklad };
    //  sklad_id     null   false
    if (sklad_id !== null && sklad_id !== false && sklad_id !== undefined) {
      body.sklad_id = sklad_id;
    }

    const headers = {
      'accept': 'application/json',
      'Content-Type': 'application/json',
    };

    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }

    const response = await fetch(url, {
      method: 'POST',
      headers,
      body: JSON.stringify(body),
    });

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
    }

    //   
    const contentType = response.headers.get('content-type');
    
    //   JSON,   JSON
    if (contentType && contentType.includes('application/json')) {
      const data = await response.json();
      //     JSON (, "token-string"),  
      if (typeof data === 'string') {
        return data;
      }
      return data;
    }
    
    //    (),   
    return await response.text();
  }

  async downloadInventoryReport(sklad, sklad_id) {
    const url = `${this.baseURL}/report/inventory/download`;
    const token = localStorage.getItem('authToken');
    
    const body = { sklad };
    //  sklad_id     null   false
    if (sklad_id !== null && sklad_id !== false && sklad_id !== undefined) {
      body.sklad_id = sklad_id;
    }

    const headers = {
      'accept': 'application/json',
      'Content-Type': 'application/json',
    };

    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }

    const response = await fetch(url, {
      method: 'POST',
      headers,
      body: JSON.stringify(body),
    });

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
    }

    //   
    const contentType = response.headers.get('content-type');
    
    //   JSON (   QR-)
    if (contentType && contentType.includes('application/json')) {
      const data = await response.json();
      return data; //  { message, url, qr, path, filename }
    }

    //    (blob)
    const blob = await response.blob();
    
    //      Content-Disposition   
    const contentDisposition = response.headers.get('Content-Disposition');
    let filename = 'inventory_report.xlsx'; //  
    if (contentDisposition) {
      const filenameMatch = contentDisposition.match(/filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/);
      if (filenameMatch && filenameMatch[1]) {
        filename = filenameMatch[1].replace(/['"]/g, '');
      }
    }

    //    
    const downloadUrl = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = downloadUrl;
    link.download = filename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    window.URL.revokeObjectURL(downloadUrl);

    return { success: true, filename };
  }

