import React, { useState } from 'react';
import '../css/styles.scss';
import '../css/Dialogs.scss';

const CreateOrganizationDialog = ({ onClose, onCreate }) => {
  const [formData, setFormData] = useState({
    name: '',
    legalName: '',
    description: '',
    inn: '',
    address: {
      country: '',
      city: '',
      street: '',
      postalCode: ''
    },
    settings: {
      currency: 'RUB',
      language: 'ru',
      timezone: 'Europe/Moscow',
      autoBackup: true,
      backupFrequency: 'DAILY'
    }
  });

  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});

  const validateForm = () => {
    const newErrors = {};

    //   ( 3 )
    if (!formData.name.trim()) {
      newErrors.name = ' ';
    } else if (formData.name.trim().length < 3) {
      newErrors.name = '    3 ';
    }

    //   (10  12 )
    if (!formData.inn.trim()) {
      newErrors.inn = ' ';
    } else {
      const innRegex = /^\d{10}$|^\d{12}$/;
      if (!innRegex.test(formData.inn)) {
        newErrors.inn = '   10  12 ';
      }
    }

    //   
    if (!formData.legalName.trim()) {
      newErrors.legalName = '  ';
    } else if (formData.legalName.trim().length < 3) {
      newErrors.legalName = '     3 ';
    }

    //  
    if (!formData.address.country.trim()) {
      newErrors.country = ' ';
    }
    if (!formData.address.city.trim()) {
      newErrors.city = ' ';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    setLoading(true);

    try {
      await onCreate(formData);
      //     
      setFormData({
        name: '',
        legalName: '',
        description: '',
        inn: '',
        address: {
          country: '',
          city: '',
          street: '',
          postalCode: ''
        },
        settings: {
          currency: 'RUB',
          language: 'ru',
          timezone: 'Europe/Moscow',
          autoBackup: true,
          backupFrequency: 'DAILY'
        }
      });
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (field, value) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
    //     
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const handleAddressChange = (field, value) => {
    setFormData(prev => ({
      ...prev,
      address: {
        ...prev.address,
        [field]: value
      }
    }));
    //      
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const handleSettingsChange = (field, value) => {
    setFormData(prev => ({
      ...prev,
      settings: {
        ...prev.settings,
        [field]: value
      }
    }));
    //      ,  
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const handleOverlayClick = (e) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  const handleContentClick = (e) => {
    e.stopPropagation();
  };

  return (
    <div className="dialog-overlay active" onClick={handleOverlayClick}>
      <div className="dialog-content create-organization-dialog" onClick={handleContentClick}>
        <div className="dialog-header">
          <h2> </h2>
          <button className="dialog-close" onClick={onClose}></button>
        </div>

        <form onSubmit={handleSubmit} className="dialog-form">
          <div className="form-grid">
              <div className="form-row">
                <label className="form-label">  *</label>
                <input
                  type="text"
                  className={`form-input ${errors.name ? 'error' : ''}`}
                  value={formData.name}
                  onChange={(e) => handleChange('name', e.target.value)}
                  placeholder="   ( 3 )"
                  required
                  minLength={3}
                  disabled={loading}
                />
                {errors.name && <div className="error-text">{errors.name}</div>}
              </div>

              <div className="form-row">
                <label className="form-label">  *</label>
                <input
                  type="text"
                  className={`form-input ${errors.legalName ? 'error' : ''}`}
                  value={formData.legalName}
                  onChange={(e) => handleChange('legalName', e.target.value)}
                  placeholder="  "
                  required
                  minLength={3}
                  disabled={loading}
                />
                {errors.legalName && <div className="error-text">{errors.legalName}</div>}
              </div>

              <div className="form-row">
                <label className="form-label"></label>
                <textarea
                  className="form-input"
                  value={formData.description}
                  onChange={(e) => handleChange('description', e.target.value)}
                  placeholder="  "
                  rows="3"
                  disabled={loading}
                />
              </div>

              <div className="form-row">
                <label className="form-label"> *</label>
                <input
                  type="text"
                  className={`form-input ${errors.inn ? 'error' : ''}`}
                  value={formData.inn}
                  onChange={(e) => handleChange('inn', e.target.value)}
                  placeholder="10  12 "
                  required
                  pattern="\d{10}|\d{12}"
                  disabled={loading}
                />
                {errors.inn && <div className="error-text">{errors.inn}</div>}
              </div>

              <div className="form-section">
                <h4> *</h4>
                <div className="form-grid" style={{ gridTemplateColumns: '1fr 1fr', gap: '12px' }}>
                  <div className="form-row">
                    <label className="form-label"> *</label>
                    <input
                      type="text"
                      className={`form-input ${errors.country ? 'error' : ''}`}
                      value={formData.address.country}
                      onChange={(e) => handleAddressChange('country', e.target.value)}
                      placeholder=""
                      required
                      disabled={loading}
                    />
                    {errors.country && <div className="error-text">{errors.country}</div>}
                  </div>
                  <div className="form-row">
                    <label className="form-label"> *</label>
                    <input
                      type="text"
                      className={`form-input ${errors.city ? 'error' : ''}`}
                      value={formData.address.city}
                      onChange={(e) => handleAddressChange('city', e.target.value)}
                      placeholder=""
                      required
                      disabled={loading}
                    />
                    {errors.city && <div className="error-text">{errors.city}</div>}
                  </div>
                  <div className="form-row">
                    <label className="form-label"></label>
                    <input
                      type="text"
                      className="form-input"
                      value={formData.address.street}
                      onChange={(e) => handleAddressChange('street', e.target.value)}
                      placeholder=". , 123"
                      disabled={loading}
                    />
                  </div>
                  <div className="form-row">
                    <label className="form-label"> </label>
                    <input
                      type="text"
                      className="form-input"
                      value={formData.address.postalCode}
                      onChange={(e) => handleAddressChange('postalCode', e.target.value)}
                      placeholder="123456"
                      disabled={loading}
                    />
                  </div>
                </div>
              </div>

              <div className="form-section">
                <h4></h4>
                <div className="form-grid" style={{ gridTemplateColumns: '1fr 1fr', gap: '12px' }}>
                  <div className="form-row">
                    <label className="form-label"></label>
                    <select
                      className="form-select"
                      value={formData.settings.currency}
                      onChange={(e) => handleSettingsChange('currency', e.target.value)}
                      disabled={loading}
                    >
                      <option value="RUB">RUB -  </option>
                      <option value="USD">USD -  </option>
                      <option value="EUR">EUR - </option>
                    </select>
                  </div>
                  <div className="form-row">
                    <label className="form-label"></label>
                    <select
                      className="form-select"
                      value={formData.settings.language}
                      onChange={(e) => handleSettingsChange('language', e.target.value)}
                      disabled={loading}
                    >
                      <option value="ru"></option>
                      <option value="en">English</option>
                    </select>
                  </div>
                  <div className="form-row">
                    <label className="form-label"> </label>
                    <select
                      className="form-select"
                      value={formData.settings.timezone}
                      onChange={(e) => handleSettingsChange('timezone', e.target.value)}
                      disabled={loading}
                    >
                      <option value="Europe/Moscow">Europe/Moscow ()</option>
                      <option value="Europe/London">Europe/London ()</option>
                      <option value="America/New_York">America/New_York (-)</option>
                    </select>
                  </div>
                  <div className="form-row form-checkbox">
                    <input
                      type="checkbox"
                      id="autoBackup"
                      checked={formData.settings.autoBackup}
                      onChange={(e) => handleSettingsChange('autoBackup', e.target.checked)}
                      disabled={loading}
                    />
                    <label htmlFor="autoBackup" className="form-label-checkbox">  </label>
                  </div>
                  {formData.settings.autoBackup && (
                    <div className="form-row">
                      <label className="form-label">  </label>
                      <select
                        className="form-select"
                        value={formData.settings.backupFrequency}
                        onChange={(e) => handleSettingsChange('backupFrequency', e.target.value)}
                        disabled={loading}
                      >
                        <option value="DAILY"></option>
                        <option value="WEEKLY"></option>
                        <option value="MONTHLY"></option>
                      </select>
                    </div>
                  )}
                </div>
              </div>
            </div>

          <div className="dialog-footer">
            <button
              type="button"
              className="btn btn-outlined"
              onClick={onClose}
              disabled={loading}
            >
              
            </button>
            <button
              type="submit"
              className="btn btn-contained"
              disabled={loading}
            >
              {loading ? '...' : ' '}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CreateOrganizationDialog;