// src/components/organization/hooks/useMembers.js
import { useCallback } from 'react';
import { apiService } from '../../../services/api';

export const useMembers = (organizationId, setMembers, setSnackbar) => {
  // 
  const showSnackbar = useCallback((message, severity = 'success') => {
    setSnackbar({ open: true, message, severity });
    setTimeout(() => {
      setSnackbar({ open: false, message: '', severity: 'success' });
    }, 3000);
  }, [setSnackbar]);

  //  
  const handleInviteMember = async (email, role) => {
    try {
      const newMember = await apiService.inviteMember(organizationId, email, role);
      setMembers(prev => [...prev, newMember]);
      showSnackbar('  ');
      return newMember;
    } catch (error) {
      console.error('   :', error);
      showSnackbar('   ', 'error');
      throw error;
    }
  };

  //  
  const handleRemoveMember = async (memberId) => {
    if (!window.confirm(' ,     ?')) {
      return;
    }

    try {
      await apiService.removeMember(organizationId, memberId);
      setMembers(prev => prev.filter(member => member.id !== memberId));
      showSnackbar('  ');
    } catch (error) {
      console.error('   :', error);
      showSnackbar('   ', 'error');
      throw error;
    }
  };

  return {
    handleInviteMember,
    handleRemoveMember
  };
};