import React from 'react';
import './Icon.scss';

/**
 *      assets/icons
 * @param {string} name -   
 * @param {string} size - : 'small' (24px), 'medium' (32px), 'large' (40px), 'xlarge' (48px)
 * @param {string} className -  CSS 
 * @param {object} style -  
 * @param {string} alt -  
 */
const Icon = ({ name, size = 'small', className = '', style = {}, alt = '' }) => {
  //     public/assets/icons
  const iconPath = `/assets/icons/${name}`;
  
  //  
  const sizes = {
    small: '24px',
    medium: '32px',
    large: '40px',
    xlarge: '48px'
  };
  
  const iconSize = sizes[size] || sizes.small;
  
  return (
    <img 
      src={iconPath} 
      alt={alt || name}
      className={`icon icon-${size} ${className}`}
      style={{ 
        width: iconSize, 
        height: iconSize, 
        objectFit: 'contain',
        display: 'inline-block',
        verticalAlign: 'middle',
        flexShrink: 0,
        ...style 
      }}
    />
  );
};

export default Icon;

