import { apiService } from './api';

class AuthService {
  constructor() {
    this.token = localStorage.getItem('authToken');
    this.user = JSON.parse(localStorage.getItem('user') || 'null');

    if (this.token) {
      apiService.setToken(this.token);
    }
  }

  /**
   *     email  
   */
  async login(credentials) {
    try {
      const response = await apiService.login(credentials);
      this.token = response.access_token;
      apiService.setToken(this.token);
      localStorage.setItem('authToken', this.token);

      //    
      const user = await this.getCurrentUser();
      this.user = user;
      localStorage.setItem('user', JSON.stringify(user));
      
      console.log('   :', user);
      return { success: true, user };
      
    } catch (error) {
      console.error('  :', error);
      return { success: false, error: error.message };
    }
  }

  /**
   *   
   */
  async register(userData) {
    try {
      const response = await apiService.register(userData);
      return { success: true, data: response };
    } catch (error) {
      console.error('  :', error);
      return { success: false, error: error.message };
    }
  }

  /**
   *    
   */
  async getCurrentUser() {
    try {
      if (!this.token) return null;
      
      const user = await apiService.getMe();
      return user;
    } catch (error) {
      console.warn('     :', error.message);
      //      
      return this._createTempUserFromToken(this.token);
    }
  }

  /**
   *   
   */
  logout() {
    this.token = null;
    this.user = null;
    apiService.clearAuth();
    localStorage.removeItem('authToken');
    localStorage.removeItem('user');
  }

  /**
   *   
   */
  isAuthenticated() {
    return !!this.token;
  }

  /**
   *   
   */
  getToken() {
    return this.token;
  }

  /**
   *   
   */
  getUser() {
    return this.user;
  }

  /**
   *  
   */
  async validateToken() {
    if (!this.token) return false;
    
    try {
      await apiService.getMe();
      return true;
    } catch (error) {
      console.warn(' :', error);
      this.logout();
      return false;
    }
  }

  /**
   *      
   */
  _createTempUserFromToken(token) {
    try {
      const payload = JSON.parse(atob(token.split('.')[1]));
      return {
        id: payload.sub || payload.user_id || 'unknown',
        email: payload.email || payload.sub || 'unknown@example.com',
        fullName: payload.username || payload.sub?.split('@')[0] || '',
        role: payload.role || 'User',
        email_verified: false
      };
    } catch (error) {
      return {
        id: 'unknown',
        email: 'unknown@example.com',
        fullName: '',
        role: 'User',
        email_verified: false
      };
    }
  }
}

export const authService = new AuthService();