import React, { useState, useEffect } from 'react';
import { useTheme } from '../../theme/ThemeContext';
import { apiService } from '../../services/api';
import '../css/styles.scss';
import '../css/Dialogs.scss';

const CreateInvitationDialog = ({ open, onClose, organizationId, onSuccess }) => {
  const { isDark } = useTheme();
  const [email, setEmail] = useState('');
  const [fullName, setFullName] = useState('');
  const [phone, setPhone] = useState('');
  const [role, setRole] = useState('User');
  const [sending, setSending] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    if (!open) {
      //    
      setEmail('');
      setFullName('');
      setPhone('');
      setRole('User');
      setError('');
    }
  }, [open]);

  /**   */
  const handleSendInvitation = async (e) => {
    e?.preventDefault();
    
    if (!email.trim()) {
      setError('Email   ');
      return;
    }

    setSending(true);
    setError('');

    try {
      //  identifier_type  identifier_value
      // : email > phone > fullName
      let identifierType = 'email';
      let identifierValue = email.trim();

      if (email.trim()) {
        identifierType = 'email';
        identifierValue = email.trim();
      } else if (phone.trim()) {
        identifierType = 'phone';
        identifierValue = phone.trim();
      } else if (fullName.trim()) {
        identifierType = 'full_name';
        identifierValue = fullName.trim();
      }

      await apiService.createInvitation(organizationId, {
        identifier_type: identifierType,
        identifier_value: identifierValue,
        role: role,
        expires_in_hours: 168 // 7   
      });

      if (onSuccess) {
        onSuccess();
      }
      onClose();
    } catch (err) {
      console.error('  :', err);
      setError(err.message || '  ');
    } finally {
      setSending(false);
    }
  };

  if (!open) return null;

  return (
    <div className={`dialog-overlay ${open ? 'active' : ''}`} onClick={onClose}>
      <div 
        className={`dialog-content create-invitation-dialog ${isDark ? 'dark-mode' : ''}`}
        onClick={(e) => e.stopPropagation()}
      >
        <div className="dialog-header">
          <h2 className="dialog-title"> </h2>
          <button className="dialog-close" onClick={onClose}></button>
        </div>

        <div className="dialog-body">
          <form onSubmit={handleSendInvitation}>
            <div className="form-group">
              <label className="form-label">Email *</label>
              <input
                type="email"
                className="form-input"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="example@mail.com"
                required
              />
            </div>

            <div className="form-group">
              <label className="form-label"> *</label>
              <input
                type="text"
                className="form-input"
                value={fullName}
                onChange={(e) => setFullName(e.target.value)}
                placeholder="  "
                required
              />
            </div>

            <div className="form-group">
              <label className="form-label"></label>
              <input
                type="tel"
                className="form-input"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                placeholder="+7 (999) 123-45-67"
              />
              <small className="form-hint"> </small>
            </div>

            <div className="form-group">
              <label className="form-label"></label>
              <select
                className="form-select"
                value={role}
                onChange={(e) => setRole(e.target.value)}
              >
                <option value="User"></option>
                <option value="Admin"></option>
              </select>
            </div>

            {error && <div className="form-error">{error}</div>}

            <div className="dialog-actions">
              <button
                type="button"
                className="btn btn-outline"
                onClick={onClose}
              >
                
              </button>
              <button
                type="submit"
                className="btn btn-primary"
                disabled={sending || !email.trim() || !fullName.trim()}
              >
                {sending ? '...' : ' '}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default CreateInvitationDialog;
