// components/CreateWarehouseDialog.jsx
import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  FormControlLabel,
  Checkbox,
  Typography
} from '@mui/material';

const CreateWarehouseDialog = ({ open, onClose, onCreate }) => {
  const [formData, setFormData] = useState({
    name: '',
    code: '',
    type: 'MAIN',
    address: {
      country: '',
      city: '',
      street: '',
      postalCode: ''
    },
    contact_person: {
      name: '',
      phone: '',
      email: ''
    },
    settings: {
      allowNegativeStock: false,
      requireApproval: false,
      autoPrintLabels: false,
      barcodeType: 'EAN13'
    }
  });

  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);

  //  email
  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  //   ()
  const validatePhone = (phone) => {
    const phoneRegex = /^\+?[1-9]\d{1,14}$/;
    return phoneRegex.test(phone.replace(/\s/g, ''));
  };

  //    ( 3 )
  const validateMinLength = (value, minLength = 3) => {
    return value && value.trim().length >= minLength;
  };

  const validateForm = () => {
    const newErrors = {};

    //     
    if (!formData.name.trim()) {
      newErrors.name = '  ';
    } else if (!validateMinLength(formData.name)) {
      newErrors.name = '    3 ';
    }

    if (!formData.code.trim()) {
      newErrors.code = '  ';
    } else if (!validateMinLength(formData.code)) {
      newErrors.code = '    3 ';
    }

    if (!formData.address.city.trim()) {
      newErrors['address.city'] = ' ';
    }

    if (!formData.address.street.trim()) {
      newErrors['address.street'] = ' ';
    }

    //  email
    if (formData.contact_person.email && !validateEmail(formData.contact_person.email)) {
      newErrors['contact_person.email'] = '  email ';
    }

    //  
    if (formData.contact_person.phone && !validatePhone(formData.contact_person.phone)) {
      newErrors['contact_person.phone'] = '   ';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!validateForm()) {
      return;
    }

    setLoading(true);
    try {
      await onCreate(formData);
      handleClose();
    } catch (error) {
      console.error('   :', error);
    } finally {
      setLoading(false);
    }
  };

  const handleClose = () => {
    //    
    setFormData({
      name: '',
      code: '',
      type: 'MAIN',
      address: {
        country: '',
        city: '',
        street: '',
        postalCode: ''
      },
      contact_person: {
        name: '',
        phone: '',
        email: ''
      },
      settings: {
        allowNegativeStock: false,
        requireApproval: false,
        autoPrintLabels: false,
        barcodeType: 'EAN13'
      }
    });
    setErrors({});
    onClose();
  };

  const handleChange = (path, value) => {
    setFormData(prev => {
      const keys = path.split('.');
      const lastKey = keys.pop();
      const nested = keys.reduce((obj, key) => obj[key], prev);
      nested[lastKey] = value;
      return { ...prev };
    });

    //     
    if (errors[path]) {
      setErrors(prev => ({ ...prev, [path]: '' }));
    }
  };

  const getError = (path) => {
    return errors[path] || '';
  };

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      maxWidth="md"
      fullWidth
    >
      <form onSubmit={handleSubmit}>
        <DialogTitle>
            
        </DialogTitle>

        <DialogContent>
          <Grid container spacing={3} sx={{ mt: 1 }}>

            {/*   */}
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom>
                 
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="  *"
                value={formData.name}
                onChange={(e) => handleChange('name', e.target.value)}
                error={!!getError('name')}
                helperText={getError('name') || ' 3 '}
                required
                inputProps={{
                  minLength: 3,
                  maxLength: 100
                }}
              />
            </Grid>

            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="  *"
                value={formData.code}
                onChange={(e) => handleChange('code', e.target.value)}
                error={!!getError('code')}
                helperText={getError('code') || ' 3 '}
                required
                inputProps={{
                  minLength: 3,
                  maxLength: 50
                }}
              />
            </Grid>

            <Grid item xs={12} md={6}>
              <FormControl fullWidth>
                <InputLabel> </InputLabel>
                <Select
                  value={formData.type}
                  label=" "
                  onChange={(e) => handleChange('type', e.target.value)}
                >
                  <MenuItem value="MAIN"></MenuItem>
                  <MenuItem value="RESERVE"></MenuItem>
                  <MenuItem value="RETAIL"></MenuItem>
                </Select>
              </FormControl>
            </Grid>

            {/*  */}
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
                
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label=""
                value={formData.address.country}
                onChange={(e) => handleChange('address.country', e.target.value)}
              />
            </Grid>

            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label=" *"
                value={formData.address.city}
                onChange={(e) => handleChange('address.city', e.target.value)}
                error={!!getError('address.city')}
                helperText={getError('address.city')}
                required
              />
            </Grid>

            <Grid item xs={12} md={8}>
              <TextField
                fullWidth
                label=",  *"
                value={formData.address.street}
                onChange={(e) => handleChange('address.street', e.target.value)}
                error={!!getError('address.street')}
                helperText={getError('address.street')}
                required
              />
            </Grid>

            <Grid item xs={12} md={4}>
              <TextField
                fullWidth
                label=" "
                value={formData.address.postalCode}
                onChange={(e) => handleChange('address.postalCode', e.target.value)}
              />
            </Grid>

            {/*   */}
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
                 
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="  "
                value={formData.contact_person.name}
                onChange={(e) => handleChange('contact_person.name', e.target.value)}
              />
            </Grid>

            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label=""
                value={formData.contact_person.phone}
                onChange={(e) => handleChange('contact_person.phone', e.target.value)}
                error={!!getError('contact_person.phone')}
                helperText={getError('contact_person.phone') || ': +79123456789'}
                placeholder="+79123456789"
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                fullWidth
                label="Email"
                type="email"
                value={formData.contact_person.email}
                onChange={(e) => handleChange('contact_person.email', e.target.value)}
                error={!!getError('contact_person.email')}
                helperText={getError('contact_person.email') || 'example@domain.com'}
                placeholder="example@domain.com"
              />
            </Grid>

            {/*  */}
            <Grid item xs={12}>
              <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
                 
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={formData.settings.allowNegativeStock}
                    onChange={(e) => handleChange('settings.allowNegativeStock', e.target.checked)}
                  />
                }
                label="  "
              />
            </Grid>

            <Grid item xs={12} md={6}>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={formData.settings.requireApproval}
                    onChange={(e) => handleChange('settings.requireApproval', e.target.checked)}
                  />
                }
                label="  "
              />
            </Grid>

            <Grid item xs={12} md={6}>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={formData.settings.autoPrintLabels}
                    onChange={(e) => handleChange('settings.autoPrintLabels', e.target.checked)}
                  />
                }
                label=" "
              />
            </Grid>

            <Grid item xs={12} md={6}>
              <FormControl fullWidth>
                <InputLabel> -</InputLabel>
                <Select
                  value={formData.settings.barcodeType}
                  label=" -"
                  onChange={(e) => handleChange('settings.barcodeType', e.target.value)}
                >
                  <MenuItem value="EAN13">EAN-13</MenuItem>
                  <MenuItem value="CODE128">CODE-128</MenuItem>
                  <MenuItem value="QR">QR Code</MenuItem>
                </Select>
              </FormControl>
            </Grid>

          </Grid>
        </DialogContent>

        <DialogActions>
          <Button onClick={handleClose} disabled={loading}>
            
          </Button>
          <Button
            type="submit"
            variant="contained"
            disabled={loading}
          >
            {loading ? '...' : ' '}
          </Button>
        </DialogActions>
      </form>
    </Dialog>
  );
};

export default CreateWarehouseDialog;